import { useState } from "react";
import Input from "./components/Input";

function App() {

  const [book, setBook] = useState({
    title: '',
    pages: '',
    publishDate: ''
  });

  const onChange = (e) => {
      setBook({
        ...book,
        [e.target.name]: e.target.value
      })
  }

  const onSubmit = (e) => {
      e.preventDefault();
      console.log(book);
  }

  return (
    <div className="App">
      <h1>Hello World</h1>
      <form onSubmit={onSubmit}>
        <Input id="bookTitle" name="Title" type="text" placeholder="Title" value={book.title} onChange={onChange} />
        <Input id="bookPageNo" name="Page Number" type="number" placeholder="Page Number" value={book.pages} onChange={onChange} />
        <Input id="bookPublish" name="Publish Date" type="date" placeholder="Publish Date" value={book.publishDate} onChange={onChange} />
        <button type="submit">Add</button>
      </form>
    </div>
  );
}

export default App;
